import React from 'react'
import {Slider} from '@mui/material'

type PropsType = {
    id: string
    value:  number | number[]
    onChange: (event: Event, value: number | number[]) => void
}

const SuperRange: React.FC<PropsType> = (props) => {

    return (
        <Slider

            sx={{ //    //  
width:'147px'
            }}
            valueLabelDisplay="auto"
            {...props} //       (value   )
        />
    )
}

export default SuperRange
